// 引入时间picker
import ruiDatePicker from '@/components/rattenking-dtpicker/rattenking-dtpicker.vue';
import {
	mapState
} from 'vuex';

export default {
	components: {
		ruiDatePicker
	},
	data() {
		return {
			carouselList: [],
			autoplay: true,
			indicatorDots: true,
			value1: '',
			value2: '',
			value3: '',
			value4: '',
			timeDifference: '',
			timeShow: false,
			params: {},
			resdata: {},
			CityCode: '1401', //CityName:'厦门',ProvinceId:'1400',ProvinceName:'福建省',Country:'中国',
		};
	},

	onLoad() {
		this.loadData();
		var date = new Date();
		var seperator1 = "-";
		var year = date.getFullYear();
		var month = date.getMonth() + 1;
		var strDate = date.getDate();
		var strHours = date.getHours();
		var strMinutes = date.getMinutes();

		if (month >= 1 && month <= 9) {
			month = "0" + month;
		}
		if (strDate >= 0 && strDate <= 9) {
			strDate = "0" + strDate;
		}
		this.value1 = year + seperator1 + month + seperator1 + strDate; //+ " " + strHours
		var strDate2 =strDate+1;
		   var cond1 = year % 4 == 0;  //条件1：年份必须要能被4整除
		    var cond2 = year % 100 != 0;  //条件2：年份不能是整百数
		    var cond3 = year % 400 ==0;  //条件3：年份是400的倍数
			var cond = cond1 && cond2 || cond3;
		if (month == 1 || month == 3 || month == 5 || month == 7 || month == 8 || month == 10 || month == 12) {
			if (strDate == 31) {
				strDate2 = 1
				month++
				if(month>12){
					month =1
					year++
				}
			}
		}
		if (month == 4 || month == 6 || month == 9 || month == 11) {
			if (strDate == 30) {
				strDate2 = 1
				month++
				if(month>12){
					month =1
					year++ 
				}
			}
		}
		if (month == 2) {
			if (strDate == 29) {
				strDate2 = 1
				month++
				if(month>12){
					month =1
					year++
				}
			}
			if(cond){
				if (strDate == 28) {
					strDate2 = 1
					month++
					if(month>12){
						month =1
						year++
					}
				}
			}
		}
		this.value2 = year + seperator1 + month + seperator1 + strDate2; //+ " " + strHours;
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		async loadData() {
			let carouselList = await this.$api.json('carouselList');
			this.carouselList = carouselList;
		},
		formSubmit() {
			var startTime = this.value1;
			var endTime = this.value2;
			var hotelName = this.value3;
			var CityCode = this.CityCode;
			// var
			var objData = {
				startTime: startTime,
				endTime: endTime,
				hotelName: hotelName,
				CityCode: CityCode,
				user: "ea6caf12111045358e50018742b4c47a",
				appkey: "c11e66b146640ba5e4e731119e8753ea",
				secretKey: "77b77bd3ab8d8f413ef4772522894b4b",
			}
			var curtime = '';
			startTime = new Date(startTime.replace(/-/g, "/"));
			endTime = new Date(endTime.replace(/-/g, "/"));
			curtime = endTime - startTime;
			var time = parseInt(curtime / (1000 * 60 * 60 * 24));
			localStorage.setItem('time', JSON.stringify(time));
			if (time < 1) {
				this.$api.msg('请重新选择日期~');
			} else {
				this.params = objData;
				this.timeShow = true;
				this.timeDifference = time;
				localStorage.setItem('params', JSON.stringify(this.params));
				uni.navigateTo({
					url: '../product/list',
				});
			}

		},
		bindChange(e) {
			this.value1 = e;
		},
		bindChange1(e) {
			this.value2 = e;
		},
		bindCancel() {

		}
	}
}
